import { Call, Home, Mail } from '@mui/icons-material';
import React from 'react'
import styled from 'styled-components';

const Container = styled.div`
 text-align: center;
 margin:50px 0;

`;
const Iconcontain = styled.div`
 display: flex;
 width:70%;
 justify-content: space-around;
 margin:20px 0 20px 250px;
`;
const Contain = styled.div`
 display: flex;
 justify-content: center;
 align-items: center;
`;
const Iconitem = styled.div`
 width: 50px;
 height: 50px;
 border-radius: 50%;
 border:1px solid;
 display: flex;
 align-items:center;
 justify-content: center;
 background-color: #00ffff;
 color:#ffff;
`;
const Input = styled.input`
 padding:10px 15px;
 width:375px;
 font-size: 16px;
 margin:0 10px;
`;
const Textarea = styled.textarea`
  padding:15px;
  width: 74%;
  height:200px;
  margin:15px;
`;
const Form = styled.form`
 display: flex;
 flex-direction: column;
 margin-bottom:50px;
`;
const Input1 = styled.input`
  padding:10px 30px;
  width:100px;
  margin:0 15px;
  border:none;
  background-color: #ff0000;
  color: #ffff;
  cursor: pointer;
  &:hover{
    background-color: transparent;
    color:black;
    border:1px solid;
  }
`;
const Input2 = styled.input`
  padding:10px 30px;
  width:100px;
  cursor: pointer;
  &:hover{
    color:#ff00ff;
  }
`;
const Buttonitem = styled.div`
  position:relative;
  right:470px;
`;

const Contact = () => {
  return (
    <Container>
      <h1>CONTACT INFORMATION</h1>
      <Iconcontain>
        <Contain>
          <Iconitem>
            <Home sx={{ fontSize: 30 }}/>
            </Iconitem>
             <h3>147,Madurai street,
                Tamil Nadu.
            </h3>
        </Contain>
        <Contain>
          <Iconitem>
            <Call sx={{ fontSize: 30 }}/>
          </Iconitem>
          <h3>+91 0478 5241 28</h3>
        </Contain>
        <Contain>
          <Iconitem>
            <Mail sx={{ fontSize: 30 }}/>
          </Iconitem>
          <h3>mail@example.com</h3>
        </Contain>
      </Iconcontain>
      
      <h1>CONTACT FORM</h1>
      <Form>
        <div>
        <Input type="text" placeholder="Name"></Input>
        <Input type="text" placeholder="Email"></Input>
        <Input type="text" placeholder="Phone"></Input>
        </div>
        <div>
        <Textarea placeholder='Enter your Message'></Textarea>
        </div>
        <Buttonitem>
        <Input1 type="submit" value="SEND"></Input1>
        <Input2 type="submit" value="CLEAR"></Input2>
        </Buttonitem>
      </Form>

    </Container>
  )
}

export default Contact;