import React from 'react'
import styled from 'styled-components';


const Contain = styled.div`
 display: flex;
`;
const Card = styled.div``

const Sample = ({allData}) => {
  console.log("particulardata",allData)
  return (
    <div>
      <Contain>
      {allData?.map((values)=>{
        return(
        <Card>
        <h4>{values?.name}</h4>
        <img src={values?.img} width="150px"></img>
        <h1>{values?.amount}</h1>
        </Card>)

      })
      }
      </Contain>
      </div>
      
      
  )   
}

export default Sample