import React from 'react'
import styled from 'styled-components';
import { FacebookOutlined, Twitter, Instagram, YouTube, Pinterest } from '@mui/icons-material';

const Container = styled.div`
  text-align:center;
  align-items: center;
  background-color:#242F47;
  color: #ffff;
  padding: 25px;
`;
const Contact = styled.div``;
const Contactinfo = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width:80%;
  font-size:1.2rem;
  font-family: 'Poppins', sans-serif;
  margin:0 0 0 150px;
  padding: 20px 0;
  align-items: center;
`;
const Title = styled.h2`
  font-size:1.5rem;
  text-align:center;
`;

const Left = styled.div``;
const Center = styled.div``;
const Right = styled.div``;
const Listitem = styled.ul`
  list-style: none;
  text-align:left;
`;
const List = styled.li`
  line-height: 35px;
  cursor: pointer;
  &:hover{
    transition:all 0.2s;
    text-decoration:underline;
    color:blue;
    }
`;
const Info = styled.li`
  line-height: 25px;
  text-align:left;
`;
const Sociallink = styled.div`
  display: flex;
  margin-left: 40px;
`;
const Input = styled.input`
  border:1px solid black;
  padding: 13px;
  width: 360px;
  border-radius: 20px;
`;
const Button = styled.button`
  padding: 13px;
  width: 200px;
  font-size: 16px;
  margin-left: 20px;
  background-color: blue;
  color:white;
  border-radius: 20px;
  border: 1px solid rgb(127, 0, 0);
  &:hover{
    transition: all 0.5s;
    background-color: #f6f8fa;
    color: black;
  };
`;
const Icon = styled.div`
  width: 30px;
  height: 30px;
  border-radius: 50%;
  color: white;
  background-color: #${(props) => props.color};
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  cursor: pointer;
  &:hover{
      color:blue;
  }
`;

const Footer = () => {
  return (
    <Container>
        <h1>SUBSCRIBE TO OUR NEWSLETTER</h1>
        <p>Get the latest updates on new products and upcoming sales</p>
        <Contact>
            <Input placeholder='enter your email address' />
            <Button> Subscribe </Button>
        </Contact>
        <Contactinfo>
        <Left>
            <Title>INFORMATION</Title>
            <Listitem>
                <List>Specials</List>
                <List>New Products</List>
                <List>My Merchandise returns</List>
                <List>Contact Us</List>
                <List>Top Sellers</List>
            </Listitem>
        </Left>
        <Center>
            <Title>MY ACCOUNT</Title>
            <Listitem>
                <List>My Account</List>
                <List>My Credits Slips</List>
                <List>My Merchandise returns</List>
                <List>My Personal info</List>
                <List>My Addresses</List>
            </Listitem>
        </Center>
        <Right>
            <Title>Store Information</Title>
            <Listitem>
                <Info><h4>Sathya Watches</h4></Info>
                <Info>Madurai</Info>
                <Info>Tamil Nadu</Info>
                <Info>+052 87 2340</Info>
                <Info>contact@example.com</Info>
            </Listitem>
            <Sociallink>
                <Icon color="3B5999">
                     <FacebookOutlined /></Icon>
                <Icon color="E4405F">
                    <Instagram />
                </Icon>
                <Icon color="55ACEE">
                    <Twitter />
                </Icon>
                <Icon color="E4405F">
                    <YouTube />
                </Icon>
                <Icon color="E60023">
                    <Pinterest />
                </Icon>
                </Sociallink>
        </Right>
        </Contactinfo>

    </Container>
  )
}

export default Footer;