import React from 'react'
import { Link } from 'react-router-dom';
import styled from "styled-components";
import data from '../Blog/Data';


const Container = styled.div`
 text-align: center;
 margin-bottom: 50px;
`;
const Contain = styled.div`
 display: flex;
 justify-content: center;
`;
const H1 = styled.h1`
  color:black;
  padding: 10px 0;
  text-align: center;
`;
const H3 = styled.h3`
  font-family: 'Poppins', sans-serif;
  font-weight: 100  ;
  cursor: pointer;
  &:hover{
      opacity:0.6;
  }
`;
const Imagecontain = styled.div`
  width: 300px;
  height: 220px;
  overflow: hidden;
  margin:0 20px;
`;
const Image = styled.img`
  cursor: pointer;
  transition:all 1s ;
  margin:15px 0;
  &:hover{
      transform: scale(1.1);
  }
`;

const Blogs = () => {
  return (
      <Container>
          <H1>LATEST BLOG POSTS</H1>
          <Contain>
            {
              data.filter(data=>data.id<4).map(values=>(
                  <div>
                  <Link to={`/Blogs/${values.id}`}><Imagecontain>
                  <Image src={values.img} width="300px"></Image>
                  </Imagecontain></Link>
                  <H3>What style of watches to choose</H3>
              </div>
  ))
            }
          </Contain>

      </Container>
  )
}

export default Blogs