import React from 'react'
import styled from 'styled-components';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { ShoppingCart,FavoriteBorder,Settings } from '@mui/icons-material';
import Item from '../../component/asset/Data';

const Container = styled.div`
  text-align: center;
  margin-top:140px;
`;
const Cart = styled.div`
  display: flex;
  justify-content: space-around;
  font-family: 'Roboto', sans-serif;
  position: relative;
  bottom:50px;
  
  
`;
const Containitem = styled.div`
  width: 20%;
  padding: 10px;
`;
const Card = styled.div`
  padding: 0 10px;
  height: 250px;
`;
const Image = styled.img`
  display: inline-block;
  width:  100%;
  height: 400px;
  object-fit: contain;
  opacity:1;
  margin-bottom: 200px;
`;
const Iconother = styled.div`
  display: flex;
  margin-top: 50px;
`;
const Iconsection = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
  height:100%;
  position: relative;
  justify-content: center;
  bottom:280px;
  opacity:0;
  transition: all 2s;
  &:hover{
    opacity: 1;
    transform: translateY(20px);
  }
`;

const Icon = styled.div`
  width:50px;
  height:50px;
  border-radius: 50%;
  background-color:#ffff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin:0 25px;
  opacity: 0.8;
  &:hover{
    opacity: 1;
  }
  
`;

const Special = () => {
  const {product} = Item;
  return (
    <Container>
      <h1>SPECIAL PRODUCTS</h1>
    <Cart>
      
      {
       product && product.map( (datas) => {
         return(
        <Containitem key={datas.id}>
          <Card>
            <Image src={datas.img1}></Image>
          </Card>
          <Iconsection>
            <Icon>
            <VisibilityIcon></VisibilityIcon>
            </Icon>
            <Iconother>
            <Icon>
              <ShoppingCart/>
              </Icon>
              <Icon>
              <FavoriteBorder/>
              </Icon>
              <Icon>
              <Settings/>
              </Icon>
            </Iconother>
          </Iconsection>
        </Containitem>
         )})}
    </Cart>
    </Container>
  )
}

export default Special;