import React from 'react'
import datas from './MenData'
import styled from 'styled-components';
import { Link } from 'react-router-dom';


const Container = styled.div`
 font-family: 'Roboto', sans-serif;
 width: 80%;
 margin:20px 0;
 margin-left: 160px;
`;
const Contain = styled.div`
 display: flex;
 flex-wrap: wrap;
 justify-content: space-around;
`;
const Card= styled.div`
 margin:10px;
 width:250px;
 height:300px;
 align-items: center;
 padding: 10px;
 font-size: 12px;
 box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5);
 &:hover{
  box-shadow: 0 4px 16px 0 rgba(0,0,0,0.5);
 }
`;
const Image = styled.img`
 width: 150px;
 height:200px;
 object-fit: contain;
 align-items: center;
 padding-left: 40px;
`;
const Button = styled.button`
 padding:10px 25px;
 font-weight: bold;
 font-size: 1rem;
 position: relative;
 bottom:180px;
 left:35px;
 cursor: pointer;
 opacity:0;
 transition: all 1s;
 &:hover{
   opacity:1;
 }
`;
const Span = styled.span`
 font-size: 1rem;
 text-decoration:line-through;
 margin:0 5px;
`;

const Men = () => {
  return (
    <Container>
      <Contain>
       {
        datas && datas.map(value => {
          return(
          <Card>
            <Image src={value.img}></Image>
            <h1>{value.name}</h1>
            <h4>Stock Now</h4>
            <h2>&#8377;{value.amount[0]}.00<Span>&#8377;{value.amount[1]}.00</Span>{value.amount[2]}%OFF</h2>
            <Link to={`/Men/${value.id}`}><Button>VIEW DETAILS</Button></Link>
           
          </Card>
          )
        })}
      </Contain>
    </Container>
  )
}

export default Men;