import { AccessTime, Person } from '@mui/icons-material'
import React from 'react';
import {useParams} from "react-router-dom";
import data from './Data';
import styled from 'styled-components';

const Container = styled.div`
 display: flex;
 justify-content: center;
 width:100%;
 text-align:center;
 align-items: center;

`;
const Contain = styled.div`
 width:70%;
 
`;
const Textbox = styled.div`
 display: flex;
 width: 40%;
 
 
`;
const Icon = styled.div`
 display: flex;
 align-items: center;
 margin: 0 10px;
`;
const Image = styled.img`
 width:70vw;
 height:80vh;
`;
const Detailbox = styled.div`
 width:100%;
 text-align: left;
`;

const BlogDetail = () => {
  const {productId} = useParams()
  const value = data?.find(prod => prod?.id === productId)
  return (
    <Container>
        
        <Contain>
        <h1>BLOG</h1>
        <Textbox>
            <h3>{value.name}</h3>
            <Icon>
                <AccessTime/>
                <h4>{value.date}</h4>
            </Icon>
            <Icon>
                <Person/>
                <h4>Posted by User</h4>
            </Icon>
        </Textbox>
        <Image src={value.img}></Image>
        <p>{value.name}</p>
        <Detailbox>
        <p>{value.detail}</p>
        <p>{value.para}</p>
        </Detailbox>
        </Contain>
    </Container>
  )
}

export default BlogDetail;