import React from 'react'
import styled from 'styled-components';

const Container = styled.div`
 font-family: 'Poppins', sans-serif;
 font-weight: 100;
 margin:25px;
 
`;
const Contain = styled.div`
  align-items: center;
  width: 90%;
  display: flex;
  justify-content: space-around;
`;
const Card = styled.div`
 box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5);
 padding: 0 20px;
 width:50%;
 margin-right:300px;
`;
const LeftContain = styled.div`
 height:200px;
 padding:15px 0;
 line-height: 35px;
`;
const ButtonBox = styled.div`
 display: flex;
 margin-top: 10px;
`;
const Button1 = styled.button``;
const Box = styled.div`
 width:40px;
 height: 20px;
 display:flex;
 justify-content: center;
 align-items: center;
 border:1px solid;
 margin:0 15px;
`;

const Section = styled.div`
 display: flex;
`;
const Left = styled.div`
 flex: 1;
`;
const Image = styled.img`
 width: 100px;
 height:110px;
`;
const Center = styled.div`
 flex:2;
`;
const TextButton = styled.div`
 display: flex;
 width:100%;

 justify-content: space-between;
`;
const Right = styled.div`
 flex:1.5;
`;
const RightContain = styled.div`
 width:40%;
 /* position: fixed; */
 width:400px;
 top:50px;
 right: 160px;
 box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5);
 margin-left: 10px;
 padding: 20px;
 line-height: 35px;
`;
const PriceContain = styled.div`
 display: flex;
 justify-content: space-between;
`;
const PriceLeft = styled.div``;
const PriceRight = styled.div``;
const Totalcontain = styled.div`
 display: flex;
 justify-content: space-between;
`;
const Span = styled.span`
 margin:0 10px;
 text-decoration: line-through;
 font-size: 14px;
`;
const H4 = styled.h4`
 cursor:pointer;
`;
const Chechbtn = styled.button`
 width:350px;
 padding: 10px 20px;
 background-color: orangered;
 color:white;
 font-size: 1em;
 border:none;
 cursor: pointer;

 
`;

const Cart = ({total,setTotal}) => {
  // console.log("particulardata",total)
  // const[]
    const handleRemove = (id) => {
    const remove = total.filter((value) => value.id !== id);
    setTotal(remove);
  };
  return (
    <Container>
      
      <Contain>
      
      <Card>
      <h3>My Cart({total.length})</h3>
      {total.map((value)=>{
        return(
        
          <LeftContain key={value.id}>
            <Section>
              <Left>
                <Image src={value.img}></Image>
                <ButtonBox>
                  <Button1 onClick={() => add(value, -1)}>-</Button1>
                  <Box>{value.quantity}</Box>
                  <Button1 onClick={() => add(value, 1)}>+</Button1>
                </ButtonBox>
              </Left>
              <Center>
                <p>{value.name}</p>
                <p>Seller:{value.seller}</p>
                <h3>&#8377;{value.amount[0]}<Span>&#8377;{value.amount[1]}</Span>{value.amount[2]}%Off</h3>
                <TextButton>
                  <h4>SAVE FOR LATER</h4>
                  <H4 onClick={() => handleRemove(value.id)}>REMOVE</H4>
                </TextButton>
              </Center>
              <Right>
                <p>Delivery by fri May 27 | Free 52</p>

              </Right>
            </Section>
            <hr></hr>
          </LeftContain>
          

)

})
}
</Card>
          <RightContain>
            <h4>PRICE DETAILS</h4>
            <hr></hr>
            <PriceContain>
              <PriceLeft>
                <p>Price ({total.length})</p>
                <p>Discount</p>
                <p>Delivery Charges</p>
              </PriceLeft>
              <PriceRight>
                <p>5265</p>
                <p>-5656</p>
                <p>FREE</p>
              </PriceRight>
            </PriceContain>
            <hr></hr>
            <Totalcontain>
              <h4>Total Amount</h4>
              <h4>55589</h4>
            </Totalcontain>
              <hr></hr>
              <h4>You will save 699769 on this order</h4>
              <Chechbtn>CHECK OUT</Chechbtn>
          </RightContain>
          
      </Contain>
      </Container>
      
      
  )   
}

export default Cart;