import React, { useState } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import Home from '../../pages/Home/Home';
import Mainnav from './Mainnav';
import Footer from './Footer';
import Men from '../../pages/Men/Men';
import Women from '../../pages/Women/Women';
import Kids from '../../pages/Kids/Kids';
import Blog from '../../pages/Blog/Blog';
import Blogs from '../../pages/Home/Blogs';
import Contact from '../../pages/Contact';
import MenDetail from '../../pages/Men/MenDetail';
import WomenDetail from '../../pages/Women/WomenDetail';
import KidsDetail from '../../pages/Kids/KidsDetail';
import BlogDetail from '../../pages/Blog/BlogDetail';
import Sample from '../../pages/Women/Sample'
import Cart from './Cart';
import Login from './Login';
import Signup from './Signup form';

const Navbar = () => {
  const [particularData,setParticularData] = useState([]);
  const [data,setData] = useState([]);
  const [particularData1,setParticularData1] = useState([]);
  const [data1,setData1] = useState([]);
  const [particularData2,setParticularData2] = useState([]);
  const [data2,setData2] = useState([]);
  // const [total,settotal] =useState([]);
  console.log("total",data)
  
  const handleClick = (item) =>{
    particularData?.push(item);
    setData(particularData => [...particularData,item]);
    console.log("item",particularData)
  }
  const handleMethod = (item) =>{
    particularData1?.push(item);
    setData1(particularData1 => [...particularData1,item]);
    console.log("itemffff",particularData1)
  }
  const handleOperate = (item) =>{
    particularData2?.push(item);
    setData2(particularData2 => [...particularData2,item]);
    console.log("itemfff",particularData2)
  }
    // settotal({data1?.concat(data2)})

let total =  [...data,...data1,...data2]
// settotal(total)

  return (
    <div>
        <BrowserRouter>
        <Mainnav particularData={total}/>
        <Routes>
            <Route index element ={<Home></Home>}/>
            <Route path = "Men" element ={<Men></Men>}/>
            <Route path = "Home" element ={<Home></Home>}/>
            <Route path = "Women" element ={<Women/>}/>
            <Route path = "Kids" element ={<Kids></Kids>}/>
            <Route path = "Blog" element ={<Blog/>}/>
            <Route path = "Contact" element ={<Contact></Contact>}/>
            <Route path = "/Men/:productId" element ={<MenDetail setParticularData={setParticularData1} handleClick={handleMethod}/>}/>
            <Route path = "/Women/:productId" element ={<WomenDetail setParticularData={setParticularData} handleClick={handleClick}/>}/>
            <Route path = "/Kids/:productId" element ={<KidsDetail setParticularData={setParticularData2} handleClick={handleOperate}/>}/>
            <Route path = "/Blog/:productId" element ={<BlogDetail></BlogDetail>}/>
            <Route path = "Blogs" element ={<Blogs/>}/>
            <Route path='card' element={<Sample/>}/>
            <Route path = "Cart" element ={<Cart total={total} setotal={total}></Cart>}/>
            <Route path = "Login" element ={<Login></Login>}/>
            <Route path = "Signup" element ={<Signup></Signup>}/>

        </Routes>
        <Footer/>
        </BrowserRouter>
        
    </div>
  )
}

export default Navbar