import { AccessTime, Message, Person } from '@mui/icons-material'
import React from 'react';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import item from './Data';


const Container = styled.div``;
const H1 = styled.h1`
 text-align:center;
`;
const Contain = styled.div`
 display: flex;
 line-height:55px;
 justify-content: center;
 margin: 45px 0;
 font-family: 'Roboto', sans-serif;
`;
const Image = styled.img`
 width:300px;
 height: 300px;
 padding: 10px;
`;
const Textbox = styled.div`
 width:40%;
`;
const Icon = styled.div`
 display: flex;
`;
const IconItem = styled.div`
 display: flex;
 align-items:center;
 margin:0 10px;
`;
const P = styled.p`
 line-height:25px;
 font-size:1.1rem;
`;
const Button = styled.button`
 padding:10px 25px;
 font-size:16px;
 background-color: #808080;
 color:white;
 border:none;
 transition: all 2s;
 &:hover{
   background-color: #101010;
 }
`;

const Blog = () => {
  return (
    <Container>
    <H1>BLOG</H1>
    {
      item && item.map(value =>{
        return(
          <Contain  key={value.id}>
      <Link to={`/Blog/${value.id}`}><Image src={value.img}></Image></Link>
        <Textbox>
          <h2>{value.name}</h2>
          <Icon>
            <IconItem>
              <AccessTime/>
              <p>{value.date}</p>
            </IconItem>
            <IconItem>
              <Message/>
              <p>{value.msg}</p>
            </IconItem>
            <IconItem>
              <Person/>
              <p>Posted by New User</p>
            </IconItem>
          </Icon>
          <P>{value.detail}</P>
          <Link to={`/Blog/${value.id}`}><Button>Read More</Button></Link>  
        </Textbox>
      </Contain>
        )
      })
}
    </Container>
  )
}

export default Blog;