import { Search,FavoriteBorder, ShoppingCart, Person } from '@mui/icons-material';
import React from 'react'
import { Link, Outlet } from 'react-router-dom'
import styled from 'styled-components';

const Container = styled.div`
  font-family: 'Poppins', sans-serif;
  background-color:#242F47;
  height: 10vh;
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  font-size: 1.3rem;
  padding: 25px 0;
  position:sticky;
  top:0;
  z-index:10;
`;
const Navitem = styled.div`
  
  
`;
const Span = styled.span`
 color:violet;
`;
const H4 = styled.h4`
 color:red;
 font-size: 2rem;
`;
const Searchitem = styled.div`
  
`;
const Input = styled.input`
  padding: 10px 30px;
  
  
`;
const H5 = styled.h5`
 position: absolute;
 font-size: 1.5rem;
 top: 10px;
 margin-left: 16px;
 width:30px;
 height:30px;
 border-radius:50%;
 background-color: red;
 display: flex;
 justify-content: center;
 align-items:center;
`;
const Icon = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  
 
`;
const Icons = styled.div`
  font-size: 10px;
  cursor:pointer;
  margin:0 5px;

 
`;
const Icons1 = styled.div`
  font-size: 10px;
  cursor:pointer;
  height:37px;
  width:40px;
  display:flex;
  color:black;
  justify-content: center;
  align-items:center;
  background-color:yellow;
  position:relative;
  right:33px;
  &:hover{
    border:1px solid;
  }

 
`;
const Searchicon = styled.div`
 display: flex;
 justify-content: center;
 align-items: center;
`;
const linkStyle = {
  margin: "1rem",
  textDecoration: "none",
  color: 'white'
}

const Mainnav = ({particularData}) => {
  return (
    <Container>
        <>
        <H4>Sathya <Span>Watches</Span></H4>
        </>
      <Navitem>
        <Link to="/Home" style={linkStyle}>Home</Link>
        <Link to="/Men" style={linkStyle}>Men</Link>
        <Link to="/Women" style={linkStyle}>Women</Link>
        <Link to="/Kids" style={linkStyle}>Kids</Link>
        <Link to="/Blog" style={linkStyle}>Blog</Link>
        <Link to="/Contact" style={linkStyle}>Contact</Link>
        <Outlet />
      </Navitem>
      <Searchitem>
            <Icon>
              <Searchicon>
                <Link to="/" style={linkStyle}><Input placeholder='Search...'/></Link>
                <Link to="/" style={linkStyle}><Icons1><Search sx={{ fontSize: 30 }}/></Icons1></Link>
              </Searchicon>
                <Link to="/Cart" style={linkStyle}><Icons><ShoppingCart/><H5>{particularData.length}</H5></Icons></Link>
                <Link to="/" style={linkStyle}><Icons><FavoriteBorder/></Icons></Link>
                <Link to="/Login" style={linkStyle}><Icons><Person/></Icons></Link>
            </Icon>
        </Searchitem>
    </Container>
  )
}

export default Mainnav