import { Star } from '@mui/icons-material';
import React from 'react';
import {useParams} from "react-router-dom"
import Menproduct from "./MenData"
import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  width: 100%;
  margin:50px 0;
  font-family: 'Roboto', sans-serif;
  
`;
const Left = styled.div`
 flex:1.5;
 align-items: center;
 text-align: center;
`;
const Center = styled.div`
 flex:1;
 margin:0 25px;
 line-height:35px;

`;
const Right = styled.div`
 flex:1;
`;
const Imagecontain = styled.div`
 display: flex;
 justify-content: center;
`;
const Span1 = styled.span`
 text-decoration: line-through;
 font-size: 17px;
 margin:0 5px;
`;
const Image = styled.img`
   width: 100px;
   border: 1px solid;
   padding: 0 10px;
   margin:0 5px;
   background-color: #ECECEC;
   &:hover{
    background-color: #ffff; 
   }
`;
const Image1 = styled.img`
  width: 300px;
`;
const Review = styled.div`
 display: flex;
 align-items: center;
`;
const Staritem = styled.div`
  display: flex;
  margin-right: 20px;
`;
const Select = styled.select`
 width:400px;
 height: 35px;
 font-size: 1rem;
 background-color: #ECECEC;
`;
const Option = styled.option``;
const Span = styled.span``;
const Input = styled.input`
 float: left;
 
`;
const Label = styled.label`
 margin: 10px 5px;
`;
const Section1 = styled.div`
 overflow-y: scroll;
 display: flex;
 flex-direction:column;
 width: 300px;
 height: 100px;
`;
const Section2 = styled.div`
 overflow-y: scroll;
 display: flex;
 flex-direction:column;
 width: 300px;
 height: 100px;
`;
const Section3 = styled.div`
 overflow-y: scroll;
 display: flex;
 flex-direction:column;
 width: 300px;
 height: 100px;
`;
const H3 = styled.h3`
 padding:10px;
 width:280px;
 background-color: #ECECEC;
`;
const ButtonContain = styled.div`
  margin-top:20px;
`;
const Button1 = styled.button`
 padding:10px 25px;
 cursor:pointer;
 font-size: 16px;
 border: none;
 color:white;
 background-color: red;
`;
const Button2 = styled.button`
 cursor: pointer;
 padding:10px 25px;
 cursor:pointer;
 border:none;
 font-size: 16px;
 margin-left:5px;
`;
const MenDetail = ({setParticularData,handleClick}) => {
  const {productId} = useParams();
  const Product = Menproduct.find(prod => prod.id === productId);
  return (
    <Container>
      <Left>
        <Image1 src={Product.img}></Image1>
        <Imagecontain>
        <Image src={Product.img}></Image>
        <Image src={Product.img1}></Image>
        <Image src={Product.img2}></Image>
        </Imagecontain>
        <ButtonContain>
        <Button1 onClick={() => {setParticularData([Product]);handleClick(Product);}}>ADD TO CART</Button1>
        <Button2 >BUY NOW</Button2>
        </ButtonContain>
        
      </Left>
      <Center>
        <h1>{Product.name}</h1>
        <Review>
          <Staritem>
            <Star/>
            <Star/>
            <Star/>
            <Star/>
            <Star/>
          </Staritem>
          <p>{Product.review} Customer Review</p>
        </Review>
        <h2>&#8377;{Product.amount[0]}.00<Span1>&#8377;{Product.amount[1]}.00</Span1>{Product.amount[2]}%OFF</h2>
        <hr/>
        <p>Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt
           ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam,
           quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat.</p>
        <h3>Color:</h3>
        <Select>
          <Option>Red</Option>
          <Option>Black</Option>
          <Option>Silver</Option>
          <Option>Dark Black</Option>
        </Select>
          <h3>Size:</h3>
          <Select>
            <Option>Large</Option>
            <Option>Medium</Option>
            <Option>Small</Option>
          </Select>
        <hr/>
        <h4>TAG<Span>:{Product.tag}</Span></h4>
        <h4>ID.NO<Span>:{Product.idno}</Span></h4>     
      </Center>
      <Right>
      <H3>CATAGORIES</H3>
        <Section1>
        <Label><Input type="checkbox"></Input>All Accessories</Label>
        <Label><Input type="checkbox"></Input>Women Watches</Label>
        <Label><Input type="checkbox"></Input>Men Watches</Label>
        <Label><Input type="checkbox"></Input>Kids Watches</Label>
        </Section1>
        <H3>BRAND</H3>
        <Section2>
        <Label><Input type="checkbox"></Input>Titan</Label>
        <Label><Input type="checkbox"></Input>Casio</Label>
        <Label><Input type="checkbox"></Input>Sonata</Label>
        <Label><Input type="checkbox"></Input>Fastrack</Label>
        <Label><Input type="checkbox"></Input>Citizen</Label>
        </Section2>
        <H3>DISCOUNT</H3>
        <Section3>
        <Label><Input type="radio"></Input>50 % and above</Label>
        <Label><Input type="radio"></Input>40 % and above</Label>
        <Label><Input type="radio"></Input>30 % and above</Label>
        <Label><Input type="radio"></Input>20 % and above</Label>
        <Label><Input type="radio"></Input>10 % and above</Label>
        </Section3>
      </Right>
      
    </Container>
  )
}

export default MenDetail;