import React from 'react'
import styled from 'styled-components';
import VisibilityIcon from '@mui/icons-material/Visibility';
import { ShoppingCart,FavoriteBorder,Settings } from '@mui/icons-material';
import Item from '../../component/asset/Data';

const Container = styled.div`
  text-align: center;
  margin: 40px 0;
`;
const Cart = styled.div`
  display: flex;
  justify-content: space-around;
  font-family: 'Roboto', sans-serif;
  margin: 30px 0;
  height:230px;
  
`;
const Containitem = styled.div`
  width: 16%;
  height:380px;
  font-family: 'Roboto', sans-serif;
  width: 20%;
  height:330px;
  background-color: #FFE4E1;
  padding: 10px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.5);
`;
const Card = styled.div`
  text-align:left;
  object-fit: cover;
  padding: 20px;
`;
const Image = styled.img`
  display: inline-block;
  width:  220px;
  height: 220px;
  object-fit: contain;
  opacity:1;
`;
const H2 = styled.div`
  font-size: 1rem;
  font-weight: 600;
  opacity: 0.8;
`;
const Span = styled.span`
  font-size: 1rem;
  text-decoration: line-through;
`;
const H3 = styled.div`
  font-size: 1.5rem;
`;
const Iconother = styled.div`
  display: flex;
  margin-top: 50px;
`;
const Iconsection = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  width:100%;
  height:100%;
  position: relative;
  justify-content: center;
  bottom:350px;
  opacity:0;
  transition: all 2s;
  &:hover{
    opacity: 1;
    transform: translateY(20px);
  }
`;

const Icon = styled.div`
  width:50px;
  height:50px;
  border-radius: 50%;
  background-color:#ffff;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin:0 25px;
  opacity: 0.8;
  &:hover{
    opacity: 1;
  }
  
`;

const Products = () => {
  const {product} = Item;
  return (
    <Container>
      <h1>FEATURED PRODUCTS</h1>
    <Cart>
      
      {
       product && product.map( (datas) => {
         return(
        <Containitem key={datas.id}>
          <Card>
            <Image src={datas.img}></Image>
            <H2>{datas.title}</H2>
            <H3>&#8377;{datas.amount}.00 <Span>&#8377;{datas.amount}.00 </Span></H3>
          </Card>
          <Iconsection>
            <Icon>
            <VisibilityIcon></VisibilityIcon>
            </Icon>
            <Iconother>
            <Icon>
              <ShoppingCart/>
              </Icon>
              <Icon>
              <FavoriteBorder/>
              </Icon>
              <Icon>
              <Settings/>
              </Icon>
            </Iconother>
          </Iconsection>
        </Containitem>
         )})}
    </Cart>
    </Container>
  )
}

export default Products