import React from 'react'
import Brandlist from "./Branddata";
import styled from "styled-components";


const Container = styled.div`
  width:100%;
  background-color:#242F47;
  color:#ffff;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align:center;
  position:relative;
  bottom:70px;
  padding: 15px 0;
`;
const Branditem = styled.div`
  color: #262626;
  display: flex;
  justify-content:center;
  align-items: center;
  width:80%;
`;
const Imagecontain = styled.div``;
const Image = styled.img`
  cursor: pointer;
  width:100px;
  height: 100px;
  margin: 0 30px;
  transition:all 2s ;
  &:hover{
    transform: scale(1.3);
  }
`;

const Brand = () => {
  return (
    <Container>
      <h1>POPULAR BRANDS</h1>
      <Branditem>
      {
        Brandlist && Brandlist.map((value)=>{
          return(
            <Imagecontain>
              <Image src={value.img} alt="" width="70%"></Image>
            </Imagecontain>
          )
        })
      }
      </Branditem>
    </Container>
  )
}

export default Brand