import React from 'react'
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import logo from '../../component/asset/Images/banner_1.jpg'
import logo1 from '../../component/asset/Images/banner_3.jpg'


const Container = styled.div`
  display: flex;
  justify-content: center;
`;
const Contain = styled.div`
  font-family: 'Poppins', sans-serif;
  display: flex;
  width: 25%;
  margin-left: 50px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 5px;
  &:hover {
    box-shadow: 0 4px 26px 0 rgba(0,0,0,0.2);
  };
`;
 
const Image = styled.img`
  
`;
const Text = styled.div`
  line-height: 50px;
  font-size: 1.8em;
  padding: 20px;
  position: relative;
  top:30px;
  opacity: 1;
  right: 180px;
  transition: all 2s;
  width:100%;
  &:hover{
    transform: translateX(200px);
    opacity: 0;
  }
`;
const H3 =styled.h3``;
const Contain1 = styled.div`
  font-family: 'Poppins', sans-serif;
  background-color:black;
  color:#ffff;
  width: 25%;
  margin-left: 50px;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  border-radius: 5px;
  &:hover {
    box-shadow: 0 4px 26px 0 rgba(0,0,0,0.2);
  };
`;
const Textbox = styled.div`
  background-color:black;
  color:#ffff;
  display: flex;
  line-height: 50px;
  text-align: center;
  flex-direction: column;
  padding: 40px 0 0 20px;
`;
const P =styled.p`
   cursor: pointer;
   color:gray;
   transition: all 1s;
   text-decoration: underline;
  &:hover{
      color:red;
      text-decoration: underline;
  }
`;

const Banner = () => {
  return (
    <Container>
        <Contain>
         <Link to="/Men"><Image src={logo}></Image></Link>
            <Text>
             <H3>MEN'S</H3>
             <H3>WATCHES</H3>
            </Text>
        </Contain>
        <Contain>
         <Link to="/Women"><Image src={logo1}></Image></Link>
            <Text>
             <H3>WOMEN'S</H3>
             <H3>WATCHES</H3>
            </Text>
        </Contain>
        <Contain1>
            <Textbox>
            <h1>SALE</h1>
            <h1>50% OFF</h1>
            <P>SHOP NOW</P>
            </Textbox>
        </Contain1>
    </Container>
  )
}

export default Banner